@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: #e4aee3;
  background-color: #e4aee3;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  font-weight: 500;
  font-family: "Quicksand", sans-serif;
  font-size: 40px;
  color: #f9f9fb;
  text-decoration: inherit;
  display: inline-block;
  overflow: hidden;
  background: linear-gradient(to right, purple, pink, white 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 275ms ease;
}
a:hover {
  background-position: 0 100%;
}

body {
  margin: 0;
  display: flex;
  background-color: #e4aee3;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
  font-family: "Poiret One", cursive;
  font-weight: 500;
}

#welcome {
  font-size: 3.2em;
  line-height: 1.1;
  font-family: "Poiret One", cursive;
  font-weight: 500;
  border-width: 20px;
  border-color: #050511;
  background-color: #e3dee2;
  box-sizing: border-box;
  border-bottom: #050511;
}

#desc {
  letter-spacing: normal;
  font-weight: 500;
  font-family: "Quicksand", sans-serif;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  cursor: pointer;
}

#booking-button {
  font-family: "Quicksand", sans-serif;
  font-size: 20px;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #e4aee3;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
  div:hover {
    box-shadow: 0 5px 15px rgba(145, 92, 182, 0.4);
  }
}

header {
  z-index: 10;
  background: #eeeeee;
  position: relative;
  height: 120px;
}

.logo {
  top: 12px;
  left: 80px;
  position: relative;
  color: white;
  font-size: 160px;
}

.comb-over {
  top: -450px;
  left: -300px;
  transform: rotate(-10deg);
  position: absolute;
  background-image: linear-gradient(176deg, #faa5ce, #ad37e4);
  width: 5000px;
  height: 300px;
}

.background {
  position: relative;
  height: 600px;
  overflow: hidden;
}

.background::after {
  content: "";
  position: absolute;
  top: -20px; /* Extend beyond the container */
  left: -20px; /* Extend beyond the container */
  right: -20px; /* Extend beyond the container */
  bottom: -20px; /* Extend beyond the container */
  background-image: url("../public/kohoffice.jpg");
  background-size: cover;
  filter: blur(8px);
  z-index: -1;
}
